import { usersAPI } from "../api/api.js";
import { profileAPI } from "../api/api.js";
import { stopSubmit } from "redux-form";

const ADD_POST = "ADD-POST";
const DELETE_POST = "DELETE-POST";
const SET_USER_PROFILE = "SET-USER-PROFILE";
const SET_STATUS = "SET-STATUS";
const SET_PHOTO = "SET-PHOTO";

let initialState = {
  postsData: [
    { id: 1, name: "Alex", likeCounts: "22", text: "Погода хорошая" },
    { id: 2, name: "Dima", likeCounts: "10" },
    { id: 3, name: "Nikita", likeCounts: "0" },
  ],

  userProfile: null,

  status: "",
};

function profileReducer(state = initialState, action) {
  if (action.type === ADD_POST) {
    let newPost = {
      id: 1,
      name: "Rick",
      text: action.postText,
      likeCounts: 0,
    };

    for (var i = 0; i < state.postsData.length; i++) {
      state.postsData[i].id += 1;
    }

    return {
      ...state,
      postsData: [newPost, ...state.postsData],
    };
  } else if (action.type === DELETE_POST) {
    state.postsData.splice(action.id, 1);
    for (var i = action.id; i < state.postsData.length; i++) {
      state.postsData[i].id -= 1;
    }

    return {
      ...state,
      postsData: [...state.postsData],
    };
  } else if (action.type === SET_USER_PROFILE) {
    return {
      ...state,
      userProfile: action.userProfile,
    };
  } else if (action.type === SET_STATUS) {
    return {
      ...state,
      status: action.status,
    };
  } else if (action.type === SET_PHOTO) {
    return {
      ...state,
      userProfile: { ...state.userProfile, photos: action.photos },
    };
  }

  return state;
}

export function addPostActionCreator(text) {
  return {
    type: ADD_POST,
    postText: text,
  };
}

export function deletePostActionCreator(id) {
  return {
    type: DELETE_POST,
    id: id,
  };
}

function setUserProfileAC(userProfile) {
  return {
    type: SET_USER_PROFILE,
    userProfile,
  };
}

function setStatusAC(status) {
  return {
    type: SET_STATUS,
    status,
  };
}

function setPhotoAC(photos) {
  return {
    type: SET_PHOTO,
    photos,
  };
}

export const getStatusThunkCreator = (userId) => {
  return async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatusAC(response.data));
  };
};

export const updateStatusThunkCreator = (status) => {
  return async (dispatch) => {
    let response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(setStatusAC(status));
    }
  };
};

export const getUserProfileThunkCreator = (userId) => {
  return async (dispatch) => {
    let response = await usersAPI.getProfile(userId);
    dispatch(setUserProfileAC(response.data));
  };
};

export const savePhotoThunkCreator = (file) => {
  return async (dispatch) => {
    let response = await profileAPI.savePhoto(file);
    if (response.data.resultCode === 0) {
      dispatch(setPhotoAC(response.data.data.photos));
    }
  };
};

export const saveInfoThunkCreator = (info, setEditMode) => {
  return async (dispatch, getState) => {
    let userId = getState().auth.id;
    try {
      let response = await profileAPI.saveInfo(info);
      if (response.data.resultCode === 0) {
        dispatch(getUserProfileThunkCreator(userId));
        setEditMode(false);
      } else {
        dispatch(stopSubmit("profile", { _error: "Некорретные URL адреса" }));
      }
    } catch (e) {
      dispatch(
        stopSubmit("profile", {
          _error: `Запрос с ошибкой - ${e.response.status}`,
        })
      );
    }
  };
};

export default profileReducer;
