import React from "react";
import cl from "./links.module.css";
import { NavLink } from "react-router-dom";

function Links() {
  return (
    <div className={cl.links}>
      <NavLink
        to={(location) => ({ ...location, pathname: "/profile" })}
        className={cl.item}
        activeClassName={
          window.location.pathname == "/react/profile" ? cl.active : null
        }
      >
        <i className="far fa-user-circle"></i>
        <div>Моя страница</div>
      </NavLink>
      <NavLink
        to={(location) => ({ ...location, pathname: "/dialogue" })}
        className={cl.item}
        activeClassName={cl.active}
      >
        <i className="fas fa-comments"></i>
        <div>Сообщения</div>
      </NavLink>
      <NavLink
        to={(location) => ({ ...location, pathname: "/friends" })}
        className={cl.item}
        activeClassName={cl.active}
      >
        <i className="far fa-address-book"></i>
        <div>Друзья</div>
      </NavLink>
      <NavLink
        to={(location) => ({ ...location, pathname: "/Users" })}
        className={cl.item}
        activeClassName={cl.active}
      >
        <i className="far fa-compass"></i>
        <div>Пользователи</div>
      </NavLink>
    </div>
  );
}

export default Links;
