import React from "react";
import "./App.css";
import HeaderContainer from "./components/Header/HeaderContainer.js";
import Links from "./components/Links/Links.js";
import ProfileContainer from "./components/Profile/ProfileContainer.js";
import LoginContainer from "./components/Login/LoginContainer.js";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { initializeAppThunkCreator } from "./redux/app-reducer.js";
import { withSuspense } from "./hoc/withSuspense.js";
import store from "./redux/redux-store.js";
import { Provider } from "react-redux";
import { BrowserRouter } from "react-router-dom";

const Friends = React.lazy(() => import("./components/Friends/Friends.js"));
const Dialogue = React.lazy(() => import("./components/Dialogue/Dialogue.js"));
const UsersAPIComponent = React.lazy(() =>
  import("./components/Users/UsersAPIComponent.js")
);

class App extends React.Component {
  componentDidMount() {
    this.props.initializeAppThunkCreator();
  }

  render() {
    if (!this.props.initialized) {
      return null;
    }

    return (
      <div>
        <HeaderContainer />
        <div className="container">
          <Links />

          <div className="main">
            <Switch>
              <Route
                path="/profile/:userId?"
                render={() => <ProfileContainer />}
              />
              <Route path="/dialogue" render={withSuspense(Dialogue)} />
              <Route path="/friends" render={withSuspense(Friends)} />
              <Route path="/users" render={withSuspense(UsersAPIComponent)} />
              <Route path="/login" render={() => <LoginContainer />} />
              <Route exact path="/" render={() => <ProfileContainer />} />
            </Switch>
          </div>
        </div>
      </div>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    initialized: state.app.initialized,
  };
};

let AppContainer = compose(
  connect(mapStateToProps, { initializeAppThunkCreator }),
  withRouter
)(App);

let MainApp = (props) => {
  return (
    <Provider store={store}>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <AppContainer />
      </BrowserRouter>
    </Provider>
  );
};

export default MainApp;
